import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Offset from '../../../../Views/Offset';
import Spinner from '../../../../Views/Spinner';

import Message from '../../../../Views/Messages/Message/Message';
import CardApplicationAutoDuplicate from '../../../../Views/CardApplicationAutoDuplicate/CardApplicationAutoDuplicate';
import Pagination from '../../../../Views/Pagination/Pagination';
import { setDataButtonBottonTrigger, smoothTop } from '../../../../helpers';

const AggregateRequestUnitList = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg } = useStoreon('tg');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });
    const [listAplication, setListAplication] = useState({
        count: 0,
        results: []
    });

    useEffect(() => {
        setDataButtonBottonTrigger(false, tg);
        dispatch('setTextHeader', state?.name_button ?? 'СПИСОК ЗАПРОСОВ НА АГРЕГАТ');
        dispatch('setActiveHeaderText', true);
        tg.BackButton.show();
        tg.BackButton.onClick(() => navigate('/button_make_request', {
            state: {
                name_button: state?.old_name_button ?? 'СОЗДАТЬ ЗАПРОС'
            }
        }));

        const paramsList = {
            url: '/api_get_requests/',
            page: 1,
            page_size: 30,
            dataRequst: res => {
                if (
                    res?.result?.info?.status !== undefined &&
                    !res?.result?.info?.status
                  ) {
                    setMessageInForm((c) => ({
                      ...c,
                      textMessage: res?.result?.info?.message,
                      colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
                    }));
                    smoothTop();
                    return;
                  }
                    const copyData = res?.result?.slice();
                    setListAplication(c => ({
                        ...c, 
                        ...res.result,
                        results: [...copyData]
                    }))
            },
        };
        dispatch('getListApplications', paramsList)
    }, [dispatch, state?.action_page_id, tg.BackButton, navigate]);

    const changePaginationPage = data => {
        const { page } = data;
        const paramsList = {
            url: '/api_get_requests/',
            page: page ?? 1,
            page_size: 30,
            dataRequst: res => {
                if (
                    res?.result?.info?.status !== undefined &&
                    !res?.result?.info?.status
                  ) {
                    setMessageInForm((c) => ({
                      ...c,
                      textMessage: res?.result?.info?.message,
                      colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
                    }));
                    smoothTop();
                    return;
                  }
                    const copyData = res?.result?.slice();
                    setListAplication(c => ({
                        ...c,
                        ...res.result,
                        results: [...copyData]
                    }))
            },
        };
        dispatch('getListApplications', paramsList)
    }

    const handlerCopiesApplication = (id) => {
    console.log(id)
    // /api_dublicate_request/" где 
    // {
    //       request_id : id  // заявки
    // }
    const paramsList = {
        url: "/api_dublicate_request/",
        request_id : id,
        setFieldValue: () => {},
        dataRequst: (res) => {
            debugger
            const copyData = res?.result?.slice();
            setListAplication(c => ({
                ...c, 
                ...res.result,
                results: [...copyData]
            }));

            if (
              res?.result?.info?.status !== undefined &&
              !res?.result?.info?.status
            ) {
              setMessageInForm((c) => ({
                ...c,
                textMessage: res?.result?.info?.message,
                colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
              }));
              smoothTop();
              return;
            }
        },
        redirectTo: (res) => {
            debugger

            navigate("/button_make_request", {
                state: {
                  ...state,
                  textMessage: res?.result?.info.message,
                  colorMessage: res?.result?.info.status ? "#555050" : "#ff0000",
                  name_button: state?.old_name_button ?? "СОЗДАТЬ ЗАПРОС",
                },
              });
        },
      };
      dispatch("getContextDublicat", paramsList);
    }

    return (
        <React.Fragment>
            {
                listAplication.results.length ?
                    <React.Fragment>
                        <Message
                            message={messageInForm.textMessage}
                            colorMessage={messageInForm.colorMessage}
                        />

                        <CardApplicationAutoDuplicate
                            listAplication={listAplication}
                            noclick
                            handlerCopiesApplication={handlerCopiesApplication}
                        />

                        {
                            listAplication.count > 30?
                                <Pagination
                                    allCount={listAplication.count}
                                    count={30}
                                    handlerChangePaginations={ changePaginationPage }
                                    currentPage={Number(listAplication.current_page)}
                                    location={'center'}
                                />
                                : null
                        }
                        <Offset mb={50} />

                    </React.Fragment>
                    : <Spinner
                        text={'У Вас нет пока заявок'}
                    />
            }

        </React.Fragment>
    )
}
export default AggregateRequestUnitList;